import Sidebar from "@/components/Proponent/Sidebar";
import Header from "@/components/Header";
// Button Components
import PrimaryButton from "@/components/Buttons/PrimaryButton";
import SecondaryButton from "@/components/Buttons/SecondaryButton";
import UtilityButton from "@/components/Buttons/UtilityButton";
import DangerButton from "@/components/Buttons/DangerButton";
// Text Components
import SecondaryHeader from "@/components/Texts/SecondaryHeader";
import TertiaryHeader from "@/components/Texts/TertiaryHeader";
import BodyText from "@/components/Texts/BodyText";

// Enhanced OverviewCard component
function OverviewCard({ value, label, icon, trend, className = "" }: { 
  value: React.ReactNode, 
  label: string, 
  icon?: React.ReactNode, 
  trend?: 'up' | 'down' | 'neutral',
  className?: string 
}) {
  const trendColors = {
    up: 'text-green-600',
    down: 'text-red-600',
    neutral: 'text-gray-500'
  };

  return (
    <div className={`
      bg-white rounded-xl shadow-sm border border-gray-200 
      flex flex-col p-6 hover:shadow-md transition-all duration-200
      ${className}
    `}>
      <div className="flex justify-between items-start mb-4">
        <div className="text-gray-500 font-medium text-sm uppercase tracking-wider">
          {label}
        </div>
        {icon && (
          <div className="p-2 rounded-lg bg-blue-50 text-blue-600">
            {icon}
          </div>
        )}
      </div>
      
      <div className="flex items-end justify-between">
        <span className="text-3xl font-bold text-gray-900">
          {value}
        </span>
        
        {trend && (
          <div className={`flex items-center text-sm ${trendColors[trend]}`}>
            {trend === 'up' ? (
              <svg className="w-4 h-4 mr-1" fill="currentColor" viewBox="0 0 20 20">
                <path fillRule="evenodd" d="M12 7a1 1 0 01-1 1H9v1h2a1 1 0 110 2H9v1h2a1 1 0 110 2H9v1a1 1 0 11-2 0v-1H5a1 1 0 110-2h2v-1H5a1 1 0 110-2h2V8H5a1 1 0 010-2h2V5a1 1 0 112 0v1h2a1 1 0 011 1z" clipRule="evenodd" />
              </svg>
            ) : trend === 'down' ? (
              <svg className="w-4 h-4 mr-1" fill="currentColor" viewBox="0 0 20 20">
                <path fillRule="evenodd" d="M5 10a1 1 0 011-1h8a1 1 0 110 2H6a1 1 0 01-1-1z" clipRule="evenodd" />
              </svg>
            ) : null}
            <span>2.5%</span>
          </div>
        )}
      </div>
    </div>
  );
}

